<template>
  <div class="main__wrappist">
    <h2 class="main__subtitle">Амфибии</h2>
    <div class="main__list">
      <li class="main__item" v-for="item in DataAmphibia" :key="item">
        <router-link
          class="main__link"
          :to="{
            name: 'SpeciesPage',
            params: {
              itemRout: `${item.routing}`,
              itemName: `${item.name}`,
              itemDescription: `${item.description}`,
              itemImg: `${item.img}`,
              itemNumber: `${item.number}`,
              itemCoord: `${JSON.stringify(item.coordinates)}`,
              itemPrevious: `${item.previous}`,
              itemRef: item.ref,
              itemInfo: item.status,
            },
          }"
          >{{ item.name }}</router-link
        >
      </li>
    </div>
    <input-species :data="DataAmphibia"></input-species>
    <router-link @click="removeLocalStorage()" class="back" to="/"
      >Обратно</router-link
    >
  </div>
</template>

<script>
//    <router-link class="main__link" :to="{path: `/animals/${item.routing}`}">{{item.name}}</router-link>
import { DataAmphibia } from "./amphibia";
import inputSpecies from "../input-species.vue";
export default {
  name: "AmphibiaData",
  components: {
    inputSpecies,
  },
  data() {
    return {
      DataAmphibia,
    };
  },

  methods: {
    setToLocalStorage() {
      localStorage.setItem("data", JSON.stringify(DataAmphibia));
      localStorage.setItem("previous", "/amphibia");
    },
    removeLocalStorage() {
      localStorage.removeItem("data");
      localStorage.removeItem("previous");
    },
  },
  mounted() {
    this.setToLocalStorage();
  },
};
</script>

<style></style>
